tr {
  font-size: 1.3rem;
  text-align: center;
  background-color: rgb(255, 255, 255);

  td {
    border: 2px solid black;
    border-collapse: collapse;
    padding: 5px 0;
  }

  .action-buttons {
    display: flex;
    justify-content: center;
    button {
      font-size: 1.2rem;
      margin: 3px;
      border: 1px solid black;
      border-radius: 5px;
      outline: none;
      :hover {
        cursor: pointer;
      }
    }
  }
}
