form {
  display: flex;
  flex-direction: column;
  width: max-content;
  font-size: 1.2rem;
  margin: 0 auto;
  padding: 20px 50px;
  border: 5px solid rgb(106, 138, 138);
  border-radius: 10px;
  background-color: rgb(141, 179, 179);

  label {
    padding-bottom: 5px;
    padding-right: 5px;
    font-weight: 500;
  }

  input,
  select {
    margin-bottom: 20px;
    font-size: 1.2rem;

    &[type="button"],
    [type="submit"] {
      padding: 10px 5px;
    }
  }

  .action-buttons {
    display: flex;
    justify-content: space-evenly;

    & [type="button"] {
      background-color: rgb(241, 86, 86);
      color: white;
      border: 2px solid red;

      &:hover {
        background-color: black;
      }
    }
  }
}
